export default {
	data() {
		return {
			taskId: '',
			taskRankList: []
		}
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1,
				animationType: 'pop-out',
				animationDuration: 200
			});
		},
		// 查看任务排名
		taskRank() {
			this.$minApi.taskRank({
				taskId: this.taskId
			}).then(data => {
				console.log(data)
				this.taskRankList=data.data
			})
		}
	},
	created() {
		
	},
	onLoad(options) {
		console.log(options)
		this.taskId = options.taskId
		this.taskRank()
	}

}
